.locateUsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: auto;
    background-color: white;
}

.googleMap {
    margin-bottom: 5%;
    width: 90%;
    height: 500px;
    border-radius: 20px;
    border-width: 1px;
    border-color: #d1d1d1;
    border-style: solid;
}